<template>
  <div>
      <div class="header">
        <span class="text">原料采购</span>
      </div>
      <div class="wrapper">
        <ul class="list" ref="list">
          <li class="item index" @click="goToMaterialIndex">
            <i class="icon"></i>
            <span>原料首页</span>
          </li>
          <li class="item order" @click="goToMyOrder">
            <i class="icon"></i>
            <span>我的订单</span>
          </li>
          <li class="item cart" @click="goToShoppingCart">
            <i class="icon"></i>
            <span>购物车</span>
          </li>
          <li class="item distribute" @click="goToDistributeOrder">
            <i class="icon"></i>
            <span>配送订单</span>
          </li>
          <li class="item inventory" @click="goToInventory">
            <i class="icon"></i>
            <span>我的库存</span>
          </li>
        </ul>
      </div>
      <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {setLoginPage} from 'common/js/mixin'

  export default {
    mixins: [setLoginPage],
    data () {
      return {
        news: []
      }
    },
    created () {
      window.onresize = () => {
        this._calcWidth()
      }
    },
    mounted () {
      this._calcWidth()
    },
    methods: {
      // 原料首页
      goToMaterialIndex() {
        this.$router.push({
          path: '/purchase/materialindex'
        })
      },
      // 我的订单
      goToMyOrder() {
        this.$router.push({
          path: '/purchase/myorder'
        })
      },
      // 购物车
      goToShoppingCart() {
        this.$router.push({
          path: '/purchase/shoppingcart'
        })
      },
      // 配送订单
      goToDistributeOrder() {
        this.$router.push({
          path: '/purchase/distributeorder'
        })
      },
      // 我的库存
      goToInventory() {
        this.$router.push({
          path: '/purchase/inventory'
        })
      },
      //根据屏幕宽度计算图标大小和间距
      _calcWidth() {
        let clientWidth = window.innerWidth
        let listNode = this.$refs.list
        let children = listNode.children
        let width,height,marginTop,marginBottom,count = 0
        for(let i = 0; i < children.length; i++){
          let child = children[i]
          count += 1
          if(clientWidth < 340) {
            width = 135
            height = 135
            marginTop = 15
            marginBottom = 15
          } else if(clientWidth < 400) {
            width = 150
            height = 150
            marginTop = 20
            marginBottom = 20
          } else {
            width = 165
            height = 165
            marginTop = 25
            marginBottom = 25
          }
          listNode.style['margin-top'] = `${marginTop}px`
          child.style['margin-bottom'] = `${marginBottom}px`
          child.style.width = `${width}px`
          child.style.height = `${height}px`
          if(count % 2) {
            child.style.left = `${(clientWidth-width*2)/3}px`
          } else {
            child.style.right = `${(clientWidth-width*2)/3}px`
          }
        }
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .header
    position fixed
    height 44px
    top 0
    left 0
    right 0
    background $color-header-background
    display flex
    justify-content center
    align-items center
    .text
      color $color-header-text
      font-size $font-size-large
      font-weight bold
  .wrapper
    position absolute
    left 0
    right 0
    top 44px
    bottom 50px
    .list
      margin-top 20px
      display flex
      justify-content space-between
      flex-wrap wrap
      .item
        width 150px
        height 150px
        border-radius 3px
        box-shadow  0 4px 15px 0 rgba(219, 219, 219, 0.4);
        margin-bottom 20px
        background #fff
        display flex
        flex-direction column
        align-items center
        position relative
        .icon
          margin-top 44px
        span
          position absolute
          bottom 25px
          font-size 13px
          color $color-text
        &.index
          .icon
            background url("~common/image/purchase/materialindex.png") no-repeat
            width 45px
            height 45px
            background-size contain
        &.order
          .icon
            background url("~common/image/purchase/myorder.png") no-repeat
            width 34px
            height 49px
            background-size contain
        &.cart
          .icon
            background url("~common/image/purchase/shoppingcart.png") no-repeat
            width 45px
            height 42px
            background-size contain
        &.distribute
          .icon
            background url("~common/image/purchase/distributeorder.png") no-repeat
            width 43px
            height 31px
            background-size contain
            margin-top 48px
        &.inventory
          .icon
            background url("~common/image/purchase/inventory.png") no-repeat
            width 42px
            height 42px
            background-size contain

</style>


